
import './App.css';
import React, {useState} from 'react';




function App() {
  const [resultado, setResultado] = useState("");
  const handClick = (e) =>{
    setResultado(resultado.concat(e.target.name));
  } 
  const clear = () =>{
    setResultado("");
  }
  const backspace = () => {
    setResultado(resultado.slice(0, resultado.length -1));
  }
  const calcular = () => {
    try {
      setResultado(eval(resultado).toString());
    } catch(err) {
      setResultado("Erro!")
    }
  }
  return (
    <div className="container">
      <form>
        <input type="text" value={resultado}></input>
      </form>

        <div className="keypad">
          <button className="blue-light" onClick={clear}id="clear">Clear</button>
          <button onClick={backspace}id="backspace">C</button>
          <button name="/" className="blue" onClick={handClick}>&divide;</button>
          <button name="7" onClick={handClick}>7</button>
          <button name="8" onClick={handClick}>8</button>
          <button name="9" onClick={handClick}>9</button>
          <button name="*" className="blue" onClick={handClick}>&times;</button>
          <button name="4" onClick={handClick}>4</button>
          <button name="5" onClick={handClick}>5</button>
          <button name="6" onClick={handClick}>6</button>
          <button name="-" className="blue" onClick={handClick}>&ndash;</button>
          <button name="1" onClick={handClick}>1</button>
          <button name="2" onClick={handClick}>2</button>
          <button name="3" onClick={handClick}>3</button>
          <button name="+" className="blue" onClick={handClick}>+</button>
          <button name="0" onClick={handClick}>0</button>
          <button name="." onClick={handClick}>.</button>
          <button className="blue-light" onClick={calcular} id="resultado">=</button>
        </div>
    </div>
  );
}

export default App;
